<template>
    <el-card v-on:click="open_drawer(item)" v-for="item in user_list" :key="item.img_url" class="songlist_card"
        :body-style="{ padding: '0px' }">
        <img style="width: 100%;" :src="item.img_url">
        <div style="padding: 5px;">
            <p style="margin:0;padding:0;">{{ item.list_name }}</p>
        </div>
    </el-card>
    <el-drawer size="70%" :title="drawer.list_name" v-model="drawer.visible" :show-close="false" style="overflow:auto;">
        <template #header="{ custom_drawer_header,close }">
            <el-page-header @back="close" :id="custom_drawer_header">
                <template #content>
                    <div class="flex items-center">
                        <span class="text-large font-600 mr-3"> Title </span>
                        <span class="text-sm mr-2" style="color: var(--el-text-color-regular)">
                            Sub title
                        </span>
                    </div>
                </template>
                <template #extra>
                    <div class="flex items-center">
                        <el-button type="primary" v-on:click="add_all" size="mini">添加全部</el-button>
                    </div>
                </template>
            </el-page-header>
        </template>
        <el-container style="height: 100%;">
            <el-header height="40px">
                <div style="height:30px;margin-bottom: 30px;padding-left: 10px;display: flex;">
                    <el-col :span="8">
                        <div class="hide_text" style="color: #606266;">标题</div>
                    </el-col>
                    <el-col :span="6">
                        <div class="hide_text" style="color: #606266;">歌手</div>
                    </el-col>
                    <el-col :span="6">
                        <div class="hide_text" style="color: #606266;">专辑</div>
                    </el-col>
                </div><br />
            </el-header>
            <el-main>
                <div v-loading="drawer.loading" element-loading-text="正在加载中..." style="padding:10px;"
                    v-infinite-scroll="drawer.load" infinite-scroll-disabled="load_disabled">
                    <el-card v-for="i in drawer.list" :key="i.title" class="r-card" body-style="padding:10px;display: flex;
                  flex-direction: row;align-items: center" shadow="hover">
                        <el-col :span="8">
                            <div class="hide_text" style="font-size: 20px;">{{ i.title }}</div>
                        </el-col>
                        <el-col :span="6">
                            <div class="hide_text" style="font-size: 14px;">{{ i.artists }}</div>
                        </el-col>
                        <el-col :span="6">
                            <div class="hide_text" style="font-size: 14px;">{{ i.album }}</div>
                        </el-col>
                        <el-button v-on:click="listen_temporary(i)" style="margin-left: 20px;">预览</el-button>
                        <el-button v-on:click="add_item(i)" type="primary" style="margin-left: 20px;">添加</el-button>
                    </el-card>
                </div>
                <p v-if="NoMore" style="text-align: center;color:#606266;">没有更多了</p>
            </el-main>
            <el-footer height="10px">
            </el-footer>
        </el-container>
    </el-drawer>
</template>

<script>

export default {
    name: 'SongList',
    props: {
    },
    data() {
        return {
            user_list: [{
                img_url: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
                list_name: "我的歌单",
            }],
            drawer: {
                list: [],
                list_name: "",
                visible: false,
                loading: false,
                load: () => { }
            }
        }
    },
    methods: {
        open_drawer(item) {
            console.log(item);
        }
    }
}
</script>

<style scoped>
.songlist_card {
    cursor: pointer;
    width: 170px;
    height: 210px;
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 10px;
    transition: 1s;
    border-radius: 10px;
}
.songlist_card:hover {
    transform: translateY(-10px);
}
.hide_text {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
</style> 