<template>
    <el-container id="main_content">
        <el-main>
            <div id="search">
                <el-input v-model="search" placeholder="请输入内容"> </el-input>
                <el-button v-on:click="search_offset = 0; search_query()" type="primary"
                    style="margin-left: 20px;"><el-icon><box-icon name="search"></box-icon></el-icon>搜索</el-button>
            </div>
            <div style="height:20px;margin-bottom: 10px;padding-left: 10px;display: flex;color: #606266;">
                <el-col :span="8">
                    标题
                </el-col>
                <el-col :span="6">
                    歌手
                </el-col>
                <el-col :span="6">
                    专辑
                </el-col>
                <div style="width: 230px;"></div>
            </div><br />
            <div v-loading="search_loading">
                <el-card v-for="i in searchlist" :key="i.title" class="r-card" body-style="padding:10px;display: flex;
                    flex-direction: row;align-items: center;height:50px;" shadow="hover">
                    <el-col :span="8">
                        <div class="hide_text" style="font-size: 20px;">{{ i.title }}</div>
                    </el-col>
                    <el-col :span="6">
                        <div class="hide_text" style="font-size: 14px;">{{ i.artists }}</div>
                    </el-col>
                    <el-col :span="4">
                        <div class="hide_text" style="font-size: 14px;">{{ i.album }}</div>
                    </el-col>
                    <el-button v-on:click="listen_temporary(i)" style="margin-left: 20px;">预览</el-button>
                    <el-button v-on:click="add_item(i)" type="primary" style="margin-left: 20px;">添加</el-button>
                </el-card>
            </div>
        </el-main>
        <el-footer>
            <div style="align-self: flex-end;">
                <el-button v-on:click="next_page" type="primary">下一页</el-button>
                <el-input v-model="page_number" placeholder="请输入页码"
                    style="width: 200px;margin-left:20px;margin-right: 20px;"></el-input>
                <el-button v-on:click="page_jump_to">跳转</el-button>
            </div>
        </el-footer>
    </el-container>
</template>

<script>
export default {
    name: 'SearchPage',
    props: {
        socket: WebSocket,
        trylisten:Function
    },
    created() {
        this.socket.addEventListener("message", this.init);
    },
    beforeUnmount() {
        this.socket.removeEventListener("message", this.init);
        console.log("Event listener has been removed.");
    },
    data() {
        return {
            searchlist: [],
            search: "",
            search_offset: 0,
        }
    },
    methods: {
        init(data) {
            let result = JSON.parse(data.data);
            console.log(result);
            this.searchlist = [];
            result.songs.forEach((item)=>{
                this.searchlist.push({
                    id:item.id,
                    title:item.name,
                    album:item.album.name,
                    artists:item.artists[0].name,
                    duration:item.duration,
                    album_img:item.album
                });
            })
        },
        search_query() {
            let argv = {
                target: "search_query",
                data: {
                    key: this.search,
                    offset:this.search_offset
                }
            };
            this.socket.send(JSON.stringify(argv));
        },
        listen_temporary(i){
            let argv = {
                target: "get_song_url",
                data: {
                    id: i.id,
                    level: "standard"
                }
            };
            this.socket.send(JSON.parse(argv));
            this.trylisten(i);
        }
    }
}
</script>

<style scoped>
@keyframes enter {
    0% {
        transform: translateY(300px);
        opacity: 0;
    }

    100% {
        transform: translateY(0%);
        opacity: 1;
    }
}

div {
    text-align: left;
    padding: 10px;
}

div #search {
    display: flex;
    align-items: center;
}

#main_content {
    height: 100%;
}

.r-card{
    border-radius: 10px;
    padding:0;
    margin-bottom: 10px;
}
</style>