<template>
    <div id="player">
        <div id="slider">
            <el-slider size="small" v-model="current" :max="duration" :show-tooltip="false"
                @change="changeLong()"></el-slider>
        </div>
        <audio :src="data.music_url" ref="audio" autoplay="" @timeupdate="getCurr"
            @pause="is_stop = true" @play="is_stop = false"></audio>
        <div id="player-ui">
            <div id="left">
                <img id="album-cover" :src="data.album_img">
                <div style="text-align: left;">
                    <p id="title">{{ data.title }}</p>
                    <p id="artist">{{ data.artist }}</p>
                </div>
            </div>
            <div id="mid">
                <box-icon name='skip-previous' color='#303133' size='md'></box-icon>
                <box-icon :name='isPlaying' color='#303133' size='lg'></box-icon>
                <box-icon name='skip-next' color='#303133' size='md'></box-icon>
            </div>
            <div style="display: flex;flex-direction:row;align-items: center;">
                <div style="display: flex;letter-spacing: 1px;">
                    <div style="">{{ toTime(current) }}</div>/
                    <div style="">{{ toTime(data.duration) }}</div>
                </div>
                <box-icon name='playlist' type='solid' color='#303133'
                    style="margin: 0 20px 0 20px;cursor: pointer;"></box-icon>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'MusicPlayer',
    props: {
    },
    data() {
        return {
            data: {
                url: "",
                album_img: "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
                title: "测试音频",
                artist: "我",
                album: "没有",
                duration: "200",
                music_url:""
            },
            current: "100",
            isPlaying: "pause"
        }
    },
    methods: {
        toTime(sec) {
            let s = sec % 60 < 10 ? ('0' + sec % 60) : sec % 60
            let min = Math.floor(sec / 60) < 10 ? ('0' + Math.floor(sec / 60)) : Math.floor(sec / 60)
            return min + ':' + s
        },
        getCurr() {this.current = parseInt(this.$refs.audio.currentTime);},
        changeLong() {
            let ct = this.current;
            if (!isNaN(ct)) {this.$refs.audio.currentTime = ct;}
        },
        plays() {
            if (this.isPlaying == "play") {
                this.$refs.audio.play();
                this.isPlaying = "pause";
            } else {
                this.$refs.audio.pause()
                this.isPlaying = "play";
            }
        },
        init(data){
            this.data = data;
            this.isPlaying = "pause";
        }
    }
}
</script>

<style scoped>
#player {
    width: 60%;
    height: 80px;
    position: absolute;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.724);
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
    bottom: 10%;
    left: 50%;
    transform: translateX(-50%);
    transition: .35s cubic-bezier(0.52, 0.02, 0.48, 1);
    z-index: 2022;
}

#player #player_icon {
    margin-top: 3px;
    font-size: 55px;
    color: rgb(255, 255, 255);
    width: 60px;
    text-align: center;
    vertical-align: middle;
    overflow: hidden;
}

#player-ui {
    width: 100%;
    height: 100%;
    overflow: hidden;
    text-align: center;
    font-size: 14px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

#player-ui #album-cover {
    height: 70%;
    border-radius: 5px;
    margin-left: 10px;
}

#player-ui audio {
    width: 90%;
    height: 50px;
    margin-bottom: 10px;
}

#player-ui #title {
    font-size: 18px;
    margin: 0;
    margin-left: 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

#player-ui #album {
    font-size: 14px;
    margin: 8px;
    font-weight: 300;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

#player-ui #artist {
    font-size: 10px;
    margin: 0;
    margin-left: 10px;
    font-weight: 300;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

#player-ui #left {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

#player-ui #mid {
    display: flex;
    flex-direction: row;
    align-items: center;
}

#slider {
    position: absolute;
    top: 0;
    width: 100%;
    transform: translateY(-9px);
}</style>